.Startup {
  background: url("../../assets/startup-bg.jpg"), #830000;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  position: relative;
}

.slogan {
  text-align: center;
  font-size: 36px;
  text-transform: uppercase;
  margin: 0px 0 20px;
  color: white;
  text-shadow: 0 0 75px #000;
  z-index: 10;
}

.controls {
  text-align: center;
  margin: 0 auto;
  width: 230px;
}

.controls button {
  width: 100%;
  margin-bottom: 10px;
  padding: 6px;
  font-size: 25px;
  color: white;
}
