.Button {
  background: linear-gradient(#2b2b2b 0%, #151515 100%);
  color: #b1b1b1;
  padding: 6px 16px;
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 22px;
  text-transform: uppercase;
  border-radius: 3px;
  cursor: pointer;
  border: none;
  outline: none;
  border-top: 2px solid #3a3939;
  box-shadow: 0 2px 2px #222;
}

.Button:hover {
  background: linear-gradient(#151515 0%, #2b2b2b 100%);
}

.Button:active {
  box-shadow: 0 2px 2px #222;
  padding: 7px 16px 5px;
}

.Button.modalControl {
  font-size: 18px;
  padding: 3px 13px;
  box-shadow: none;
  border-bottom: 2px solid #1b1b1b;
  color: #ddd;
}

.Button.Small {
  font-size: 18px;
  padding: 3px 13px;
  box-shadow: none;
  border-bottom: 2px solid #1b1b1b;
  color: #ddd;  
}
