.End {
  background: url("../../assets/game-bg.jpg") no-repeat, #830000;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  position: relative;
}

.MainWindow {
  width: 270px;
  background: rgba(255, 255, 255, 0.95);
  margin: 60px auto 30px;
  text-align: center;
  border-radius: 5px;
  box-shadow: 0 0 100px #000;
  color: #333;
  border-bottom: 2px solid #bbb;
  border-top: 2px solid #fff;
}

.ScoreSection {
  padding: 20px 0 15px;
  width: 75%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.ScoreText p {
  margin: 0;
  font-size: 22px;
  line-height: 1.4;
  text-transform: uppercase;
  text-align: left;
}

.Divider {
    margin: 0 auto;
    width: 80%;
    border: none;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #555;    
}

.Controls {
  margin: 20px 0 15px;
}

.Controls button,
.Controls button:active {
  width: 60%;
  margin: 0 auto 10px;
  color: white;
  font-size: 19px;
  padding: 5px 0px;
}
